<template>
  <div class="chat-room-sidebar bg-dark text-white d-flex flex-column">
    <div class="chat-header d-flex justify-content-between align-items-center p-2 border-bottom">
      <h5 class="mb-0">{{ mapName }} ËÅäÂ§©ÂÆ§</h5>
      <button class="btn btn-outline-light btn-sm" @click="$emit('close')">Èö±ËóèËÅäÂ§©ÂÆ§</button>
    </div>

    <!-- ËÅäÂ§©Ë®äÊÅØÂçÄÂ°äÔºåÂèØÊªæÂãïÔºå‰∏çÊúÉÊíêÂ§ßÁï´Èù¢ -->
     
    <div class="flex-grow-1 overflow-auto p-2 border-bottom chat-messages" ref="chatContainer">
        <!-- ÊØè‰∏ÄÂâáË®äÊÅØ -->
        <div v-for="(msg, index) in messages" :key="index" class="d-flex align-items-start mb-2" :ref="index === messages.length - 1 ? 'lastMessage' : null">
            <!-- Â∑¶ÂÅ¥È†≠ÂÉèÔºö‰ΩøÁî®ËÄÖÂêçÁ®±È¶ñÂ≠óÊØç -->
            <div class="avatar bg-success text-white rounded-circle d-flex justify-content-center align-items-center me-2" style="width: 32px; height: 32px;">
                {{ msg.username.charAt(0).toUpperCase() }}
            </div>
            <!-- Ë®äÊÅØÊú¨È´î -->
            <div class="flex-grow-1">
            <!-- ‰ΩøÁî®ËÄÖÂêçÁ®±ËàáÊôÇÈñì -->
                <div class="d-flex justify-content-between small text-muted">
                    <strong class="text-light">{{ msg.username }}</strong>
                    <span style="color: #cccccc">{{ formatTime(msg.timestamp) }}</span>
                </div>
                <!-- Ë®äÊÅØÂÖßÂÆπ -->
                <div class="text-white">{{ msg.message }}</div>
            </div>
        </div>
      
    </div>

    <!-- ‰∏ãÊñπËº∏ÂÖ•ÂçÄÂüüÔºàÂåÖÂê´Ëº∏ÂÖ•Ê°ÜËàáÂÇ≥ÈÄÅÊåâÈàïÔºâ -->
      <div class="p-2 border-top">
        <div class="input-group">
          <input
            v-model="newMessage"
            @keyup.enter="sendMessage"
            placeholder="Ëº∏ÂÖ•Ë®äÊÅØ..."
            class="form-control"
          />
          <button class="btn btn-primary" @click="sendMessage">ÂÇ≥ÈÄÅ</button>
        </div>
    </div> 
  </div>
</template>

<script>
import { io } from "socket.io-client";

export default {
  props: {
    mapName: String, // Âú∞ÂúñÂêçÁ®±ÔºàËÅäÂ§©ÂÆ§ÊàøÈñìÔºâ
    username: String // ‰ΩøÁî®ËÄÖÂêçÁ®±
  },
  data() {
    return {
      socket: null,         // Socket.IO ÂÆ¢Êà∂Á´ØÂØ¶‰æã
      newMessage: "",      // Áï∂ÂâçËº∏ÂÖ•ÁöÑË®äÊÅØÊñáÂ≠ó
      messages: []  ,        // ÊâÄÊúâËÅäÂ§©ÂÆ§Ë®äÊÅØË≥áÊñô
      justSent: false,        //ÂâõÂÇ≥ÈÄÅ„Ñâ
    };
  },
  mounted() {
    // Âª∫Á´ãËàáÂæåÁ´ØÁöÑ WebSocket ÈÄ£Á∑ö
    this.socket = io();
    // Âä†ÂÖ•‰ª• mapName ÂëΩÂêçÁöÑËÅäÂ§©ÂÆ§ÊàøÈñì
    this.socket.emit("join", { map: this.mapName });

    // Êé•Êî∂ÂæûÂæåÁ´Ø‰æÜÁöÑË®äÊÅØ‰∏¶È°ØÁ§∫Ôºà‰∏çÂÜçËá™ÂãïÊç≤Âà∞Â∫ïÔºâ
    this.socket.on("chat", (msg) => {
      this.messages.push(msg);
      if (this.justSent){
        this.$nextTick(() => {
            this.scrollToBottom();
        });
      }
       this.justSent = false;
    });
    
    setTimeout(() => this.scrollToBottom(), 500);
    setTimeout(() => this.scrollToBottom(), 1500);
  },
  methods: {
    scrollToBottom() {
        this.$nextTick(() => {
            setTimeout(() => {
                requestAnimationFrame(() => {
                    const container = this.$refs.chatContainer;
                    if (container) {
                        container.scrollTop = container.scrollHeight;
                    }
                });
            },300);
        });
    },

    // ÁôºÈÄÅË®äÊÅØËá≥ÂæåÁ´Ø
    sendMessage() {
      if (!this.newMessage.trim()) return; // Á©∫ÁôΩË®äÊÅØ‰∏çÈÄÅÂá∫
      this.socket.emit("chat", {
        map: this.mapName,
        username: this.username,
        message: this.newMessage
      });
      this.newMessage = ""; // Ê∏ÖÁ©∫Ëº∏ÂÖ•Ê¨Ñ
      this.justSent=true;
    },
    
    // ÊôÇÈñìÊ†ºÂºèÂåñÈ°ØÁ§∫
    formatTime(timestamp) {
      const d = new Date(timestamp);
      return d.toLocaleTimeString();
    }
  }
};
</script>

<style scoped>
.chat-room-sidebar {
  position: fixed;
  top: 56px; /* ÂÅáË®≠ navbar È´òÂ∫¶ÊòØ 56px */
  right: 0;
  width: 350px;
  height: calc(100vh - 56px);
  display: flex;
  flex-direction: column;
  z-index: 1050;
  box-shadow: -2px 0 10px rgba(0, 0, 0, 0.5);
}

.chat-messages {
  flex: 1;
  overflow-y: auto;
}
</style>