<template>
  <NavBar :title="`${mapName} - ÈÅìÂÖ∑ÊïôÂ≠∏`" />
  <div class="esports-bg text-white min-vh-100 py-5">

  <!-- ‰∏ªÈ†ÅÈù¢ÂÆπÂô® --> 
  <div class="container py-4 text-white esports-bg position-relative">
    <!-- Âú∞ÂúñÂúñÂÉè + Â∑•ÂÖ∑ÂàóÂçÄÂ°ä -->
    <div class="card esports-card p-3 bg-dark mb-4 position-relative">
      <!-- Â∑•ÂÖ∑ÂàóÔºàÂµåÂÖ•Âú∞ÂúñÂç°Áâá‰∏äÊñπÔºâ -->
      <div class="d-flex justify-content-between align-items-center flex-wrap gap-2 mb-3">
        <div class="d-flex align-items-center">
          <label for="grenadeSelect" class="form-label text-light me-2 mb-0">ÈÅ∏ÊìáÈÅìÂÖ∑Ôºö</label>
          <select id="grenadeSelect" class="form-select bg-dark text-light border-secondary w-auto" v-model="selectedGrenade">
            <option disabled value="">Ë´ãÈÅ∏ÊìáÈÅìÂÖ∑</option>
            <option v-for="type in grenadeTypes" :key="type">{{ type }}</option>
          </select>
        </div>
        <!-- Êñ∞Â¢ûÈªû‰Ωç‰∏üÊ≥ïÊåâÈàï -->
        <div class="d-flex gap-2">
          <button 
            v-if="selectedGrenade && account_session" 
            class="btn btn-outline-info" 
            :class="{ active: isAddP}" 
            @click="isAddP = true; selectedMethodPanelVisible = false;showAddForm = false"
            >‚ûï Êñ∞Â¢ûÈªû‰Ωç‰∏üÊ≥ï
          </button>
          <button
            v-if="selectedPoint && account_session"
            class="btn btn-outline-warning "
            :class="{ active: showAddForm}"
            @click="showAddForm = true; selectedMethodPanelVisible = false;isAddP = false"
          >
            ‚ûï Êñ∞Â¢û‰∏üÊ≥ï
          </button>
        </div>
      </div>

      <!-- Âú∞ÂúñÂúñÂÉèËàáÊ®ôË®ò -->
      <div class="position-relative mx-auto" style="max-width: 800px">
        <img 
          :src="`/maps/${mapName.toLowerCase()}.png`" 
          class="img-fluid"
          @click="MapClick"
        />
        <!-- Èªû‰ΩçÊ∏≤Êüì -->
        <button
          v-for="(point, index) in points"
          :key="index"
          class="btn btn-info position-absolute translate-middle marker "
          :class="{ active: selectedPoint?.name === point.name }"
          :style="{ left: point.x + 'px', top: point.y + 'px' }"
          @click="selectUsagePoint(point)"
          :title="point.name"
        >üìç</button>
        <button
          v-if="tempxy && tempxy.x !== undefined && tempxy.y !== undefined"
          class="position-absolute translate-middle marker-temp"
          :style="{ left: tempxy.x + 'px', top: tempxy.y + 'px'}"
          :title="Êö´Â≠òÈªû"
          disabled
        >üìå</button>
        
      </div>
    </div>

    <!-- Èªû‰Ωç‰∏üÊ≥ïË°®ÂñÆ -->
    <div v-if="showAddPForm && tempxy.x !== undefined" class="position-fixed top-50 start-50 translate-middle bg-dark text-white p-4 rounded shadow-lg">
      <div class="d-flex justify-content-between align-items-center mb-3">
        <h5 class="text-info">Êñ∞Â¢ûÈªû‰Ωç‰∏üÊ≥ï</h5>
        <button class="btn-close btn-close-white" @click="showAddPForm = false"></button>
      </div>
      <input class="form-control mb-2" v-model="newPostionName" placeholder="Èªû‰ΩçÂêçÁ®±" />
      <input class="form-control mb-2" v-model="newMethodName" placeholder="‰∏üÊ≥ïÂêçÁ®±" />
      <input class="form-control mb-2" v-model="newMethodURL" placeholder="ÂΩ±ÁâáÁ∂≤ÂùÄ" />
      <div class="d-flex gap-2">
        <button class="btn btn-success" @click="submitNewPostionMethod()">ÈÄÅÂá∫</button>
        <button class="btn btn-secondary" @click="cancel_npm()">ÂèñÊ∂à</button>
      </div>
    </div>

    <!-- ‰∏üÊ≥ïÊñ∞Â¢ûË°®ÂñÆ -->
    <div v-if="showAddForm" class="position-fixed top-50 start-50 translate-middle bg-dark text-white p-4 rounded shadow-lg">
      <div class="d-flex justify-content-between align-items-center mb-3">
        <h5 class="text-info">Êñ∞Â¢û‰∏üÊ≥ïÔºö{{ selectedPoint.name }}</h5>
        <button class="btn-close btn-close-white" @click="showAddForm = false"></button>
      </div>
      <input class="form-control mb-2" v-model="newMethodName" placeholder="‰∏üÊ≥ïÂêçÁ®±" />
      <input class="form-control mb-2" v-model="newMethodURL" placeholder="ÂΩ±ÁâáÁ∂≤ÂùÄ" />
      <div class="d-flex gap-2">
        <button class="btn btn-success" @click="submitNewMethod">ÈÄÅÂá∫</button>
        <button class="btn btn-secondary" @click="showAddForm = false">ÂèñÊ∂à</button>
      </div>
    </div>

    <!-- ÂΩàÂá∫Âºè‰∏üÊ≥ïÈÅ∏ÂñÆ -->
    <div 
      v-if="selectedPoint && selectedPoint.methods.length > 1 && selectedMethodPanelVisible"
      class="position-fixed top-50 start-50 translate-middle bg-dark text-white p-4 rounded shadow-lg"
      style="z-index: 1050; min-width: 300px"
    >
      <div class="d-flex justify-content-between align-items-center mb-3">
        <h5 class="text-info mb-0">ÈÅ∏Êìá‰∏üÊ≥ïÔºö{{ selectedPoint.name }}</h5>
        <button class="btn-close btn-close-white" @click="selectedMethodPanelVisible = false"></button>
      </div>
      <select class="form-select" v-model="selectedMethod" @change="fetchVideoFromPoint">
        <option disabled value="">Ë´ãÈÅ∏Êìá‰∏üÊ≥ï</option>
        <option v-for="m in selectedPoint.methods" :key="m.name" :value="m.video_url">
          {{ m.name }}
        </option>
      </select>
    </div>

    <!-- ÂΩ±ÁâáÊí≠ÊîæÂçÄ -->
    <div v-if="videoUrl" class="card bg-dark text-white p-3 mb-4">
      <h5 class="text-info mb-3">üéÆ ÂΩ±ÁâáÊí≠Êîæ</h5>
      <div class="ratio ratio-16x9">
        <iframe
          v-if="videoUrl.includes('youtube')"
          :src="embedYoutube(videoUrl)"
          allowfullscreen
        ></iframe>
        <video v-else class="w-100" controls :src="videoUrl"></video>
      </div>
    </div>
  </div>

  <div>
    <!-- Áï∂ËÅäÂ§©ÂÆ§Ë¢´ÈóúÈñâÔºåÈ°ØÁ§∫Âè≥‰∏ãËßíÊåâÈàï -->
    <button
      v-if="!showChat"
      class="btn btn-secondary position-fixed bottom-0 end-0 m-3 z-3"
      @click="showChat = true"
    >
      È°ØÁ§∫ËÅäÂ§©ÂÆ§
    </button>

    <!-- ËÅäÂ§©ÂÆ§ÂÖÉ‰ª∂ -->
    <ChatRoom
      v-if="showChat"
      :mapName="mapName"
      :username="username"
      @close="showChat = false"
    />
  </div>
  </div>
</template>


<script>
import NavBar from '@/components/NavBar.vue'
import ChatRoom from '@/components/ChatRoom.vue'
import { useUserStore } from '@/stores/user'
import axios from 'axios';
export default {
  components: { NavBar,ChatRoom },
  props: ['mapName'],
  data() {
    return {
      selectedGrenade: '',        // ÁõÆÂâçÈÇÑÂèØ‰øùÁïôÔºàÂèØÂä†ÂàáÊèõÈ°ûÂûãÂäüËÉΩÔºâ
      selectedPoint: null,        // ‰ΩøÁî®ËÄÖÈªûÈÅ∏ÁöÑÈªû‰ΩçÔºàÂåÖÂê´ÂêçÁ®±Ëàá methodsÔºâ
      selectedMethod: '',         // Ë¢´ÈÅ∏‰∏≠ÁöÑÂΩ±ÁâáÁ∂≤ÂùÄ
      videoUrl: '',               // ÁúüÊ≠£Êí≠ÊîæÁöÑÂΩ±Áâá URL
      points:[],
      selectedMethodPanelVisible: false,
      grenadeTypes: ['smoke', 'flash', 'molotov'],
      tempxy:null,
      isAddP:false,
      showAddPForm: false,
      showAddForm: false,
      showChat: true,
      newMethodName: '',
      newMethodURL: '',
    };
  },

  
  watch: {
    selectedGrenade(newVal) {
        if (newVal) {
        // Ê†πÊìöÁõÆÂâçÈÅ∏ÁöÑÂú∞ÂúñËàáÈÅìÂÖ∑ÔºåÂêëÂæåÁ´ØÂèñÂæóÈªû‰ΩçË≥áË®ä
        axios.get(`/maps/${this.mapName}/${newVal}/points`)
            .then(res => {
            this.points = res.data;
            this.selectedPoint = null;
            this.selectedMethod = '';
            this.videoUrl = '';
            this.tempxy=null;
            this.isAddP=false;
            this.showAddPForm= false;
            this.showAddForm=false;
            })
            .catch(err => {
            console.error('ËºâÂÖ•Èªû‰ΩçÂ§±ÊïóÔºö', err);
            });
        } else {
        this.points = [];
        }
    }
  },
  
  computed: {
    // ‚úÖ Ë®àÁÆóÂ±¨ÊÄß‰æÜÊ±∫ÂÆöÊòØÂê¶ÁôªÂÖ•
    account_session() {
      const userStore = useUserStore()
      return userStore.isLoggedIn && ( userStore.role === 'admin' || userStore.role === 'user')
    },
    username(){
      return useUserStore().username
    }
    
  },

  methods: {
    selectUsagePoint(point) {
        this.selectedPoint = point;
        this.selectedUsage = '';
        this.videoUrl = '';
        this.showAddForm = false;
        this.showAddPForm = false;
        this.newPostionName = ''
        this.newMethodName = '';
        this.newMethodURL = '';
        this.isAddP=false;
        this.selectedMethodPanelVisible = point.methods?.length > 1;

        // Â§öÁ®Æ‰∏üÊ≥ï => È°ØÁ§∫ÈÅ∏ÂñÆ
        if (point.methods.length > 1) {
            this.usages = point.methods;
            this.tempxy = null
            }
        // ‰∏ÄÁ®Æ‰∏üÊ≥ï => Áõ¥Êé•È°ØÁ§∫ÂΩ±Áâá
        else if (point.methods.length === 1) {
            this.videoUrl = point.methods[0].video_url;
            this.tempxy = null
        }
    },
    isYoutube(url) {
        return url.includes('youtube.com') || url.includes('youtu.be');
    },
    embedYoutube(url) {
        if (url.includes('/shorts/')) {
        const id = url.split('/shorts/')[1].split('?')[0];
        return `https://www.youtube.com/embed/${id}`;
        } else if (url.includes('watch?v=')) {
        const id = url.split('watch?v=')[1].split('&')[0];
        return `https://www.youtube.com/embed/${id}`;
        } else if (url.includes('youtu.be/')) {
        const id = url.split('youtu.be/')[1].split('?')[0];
        return `https://www.youtube.com/embed/${id}`;
        } else {
        return ''; // ÁÑ°ÊïàÁ∂≤ÂùÄËôïÁêÜ
        }
    },
    
    fetchVideoFromPoint() {
      this.videoUrl = this.selectedMethod;
      this.selectedMethodPanelVisible = false;
    },

    submitNewMethod() {
        if (!this.newMethodName || !this.newMethodURL) {
            alert('Ë´ãÂ°´ÂØ´ÂÆåÊï¥Ë≥áË®ä');
            return;
        }

        const payload = {
            map: this.mapName,
            grenade_type: this.selectedGrenade,
            point_name: this.selectedPoint.name,
            x: this.selectedPoint.x,
            y: this.selectedPoint.y,
            method: {
            name: this.newMethodName,
            video_url: this.newMethodURL
            }
        };

        axios.post('/submit_method', payload)
            .then(() => {
            alert('ÈÄÅÂá∫ÊàêÂäüÔºåÁ≠âÂæÖÂØ©Ê†∏');
            this.showAddForm = false;
            this.newMethodName = '';
            this.newMethodURL = '';
            })
            .catch(err => {
            console.error('ÈÄÅÂá∫Â§±Êïó', err);
            alert('ÁôºÁîüÈåØË™§ÔºåË´ãÁ®çÂæåÂÜçË©¶');
            });
    },

    MapClick(event){
      if (!this.isAddP) return;
      this.showAddForm = false;
      this.showAddPForm = true;
      this.videoUrl=''
      const rect = event.target.getBoundingClientRect();
      const x = Math.round(event.clientX - rect.left);
      const y = Math.round(event.clientY - rect.top);
      this.tempxy = {x,y}
      this.selectedPoint=null
    },
    cancel_npm(){
      this.tempxy = null;
      this.showAddPForm = false;
      this.isAddP=false;
    },
    submitNewPostionMethod() {
        if (!this.newPostionName||!this.newMethodName || !this.newMethodURL) {
            alert('Ë´ãÂ°´ÂØ´ÂÆåÊï¥Ë≥áË®ä');
            return;
        }
        const {x,y} = this.tempxy
        const payload = {
            map: this.mapName,
            grenade_type: this.selectedGrenade,
            point_name: this.newPostionName,
            x,
            y,
            method: {
            name: this.newMethodName,
            video_url: this.newMethodURL
            }
        };

        axios.post('/submit_method', payload)
            .then(() => {
            alert('ÈÄÅÂá∫ÊàêÂäüÔºåÁ≠âÂæÖÂØ©Ê†∏');
            this.isAddP=false;
            this.tempxy=null;
            this.showAddPForm = false;
            this.newPostionName = '';
            this.newMethodName = '';
            this.newMethodURL = '';
            })
            .catch(err => {
            console.error('ÈÄÅÂá∫Â§±Êïó', err);
            alert('ÁôºÁîüÈåØË™§ÔºåË´ãÁ®çÂæåÂÜçË©¶');
            });
    }
  }
};
</script>

<style scoped>

.esports-card {
  background-color: #1e2734;
  border: 1px solid #2c384a;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.esports-bg {
  background: linear-gradient(135deg, #0d1117, #1a1f2b);
  font-family: 'Orbitron', sans-serif;
}

.marker {
  width: 28px;
  height: 28px;
  background-color: #68a4ff4b;
  border: 2px solid rgba(255, 255, 255, 0.8);
  border-radius: 50%;
  text-align: center;
  line-height: 20px;
  font-size: 16px;
  color: white;
  box-shadow: 0 0 8px rgba(1, 124, 238, 0.878);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.2s ease-in-out;
  backdrop-filter: blur(2px);
}
.marker:hover {
  transform: scale(1.15);
  box-shadow: 0 0 12px rgba(102, 178, 255, 0.9);
}
.marker.active {
  background-color: rgba(13, 202, 240, 0.85);
  box-shadow: 0 0 16px rgba(13, 202, 240, 0.8), 0 0 8px rgba(255, 255, 255, 0.6);
}
.marker-temp {
  width: 30px;
  height: 30px;
  background-color: rgba(255, 193, 7, 0.8);
  border: 2px solid rgba(255, 255, 255, 0.8);
  border-radius: 50%;
  text-align: center;
  line-height: 20px;
  font-size: 16px;
  color: black;
  box-shadow: 0 0 5px rgba(255, 193, 7, 0.6);
  pointer-events: none;
}



</style>