      <div class="modal-content">
        <!-- Ê®ôÈ°åÂàó -->
        <div class="modal-header">
          <h5 class="modal-title">ÁôªÂÖ•</h5>
          <button type="button" class="btn-close" @click="$emit('close')"></button>
        </div>

        <!-- Ë°®ÂñÆÂçÄ -->
        <div class="modal-body">
          <div class="mb-3">
            <label class="form-label">Â∏≥Ëôü</label>
            <input v-model="username" type="text" class="form-control" placeholder="Ë´ãËº∏ÂÖ•Â∏≥Ëôü" />
          </div>

          <div class="mb-3">
            <label class="form-label">ÂØÜÁ¢º</label>
            <input v-model="password" type="password" class="form-control" placeholder="Ë´ãËº∏ÂÖ•ÂØÜÁ¢º" />
